* {
  font-family: Arial, Helvetica, sans-serif;
}

body {
  padding: 60px;
}

input {
  font-size: 1.2rem;
  width: 20%;
  height: auto;
  border: none;
  border-bottom: 1px solid grey;
  padding: 5px;
}
input:focus {
  outline: none;
}

.grid-card {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 20px;
}

.card {
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  margin-bottom: 10px;
  margin: 10px;
  overflow: hidden;
  width: 100%;
}

.card p {
  padding: 5px;
  text-align: center;
}

.card img {
  width: 100%;
  height: 200px;
  object-fit: cover;
}
